import React from 'react'
import axios from 'axios'
import Link from 'gatsby-link'
import FeatherIcon from '../components/FeatherIcon'

class Request extends React.Component {
  constructor() {
    super()
    this.state = {
      loading: false,
      success: false,
      hasDomain: null
    }
    this.handleSubmit = this.handleSubmit.bind(this)
    this.handleChange = this.handleChange.bind(this)
  }
  handleChange(e) {
    if (e.target.name === 'hasDomain') {
      if (e.target.value === 'domainYes') {
        this.setState({
          hasDomain: true
        })
      } else {
        this.setState({
          hasDomain: false
        })
      }
    }
  }
  handleSubmit(e) {
    e.preventDefault()
    this.setState({
      loading: true,
    })
    axios.post('/mail', {
      name: e.target.name.value,
      email: e.target.email.value,
      fields: `
        Nome: ${e.target.name.value}
        Email: ${e.target.email.value}
        Telefone ou Skype: ${e.target.phone.value}
        Empresa: ${e.target.company.value || 'no informado'}
        CPF ou CNPJ: ${e.target.registration.value}
        Tem domnio: ${e.target.hasDomain.value}
        Nome do domnio: ${e.target.domain.value}
      `
    }).then((response) => {
      this.setState({
        loading: false,
        success: true
      })
    }).catch((err) => {
      this.setState({
        loading: false,
      })
    })
  }
  render() {
      return (
        <div style={{width: '100%', height: '100vh'}}>
          <section className="py-5">
            <div className="container d-flex align-items-center" style={{height: '100%', zIndex: 1, position: 'relative'}}>
              <div className="row">
                <div className="col-12 col-md-11">
                  {this.state.success ? (
                    <div>
                      <h3></h3>
                      <h1 className="font-weight-light">Obrigado! Retornaremos em breve.</h1>
                    </div>
                  ) : (
                    <div><h3></h3>
                    <h1 className="font-weight-light">Adquira o seu</h1>
                    <p>
                      Preencha os campos abaixo e aguarde nosso contato.
                    </p>
                    <p className="text-muted"><small>* Campos obrigatrios</small></p>
                    <form onSubmit={this.handleSubmit} className="mb-3">
                      <div className="form-group">
                        <label>Nome *</label>
                        <input type="text" name="name" className="form-control" required/>
                      </div>
                      <div className="form-group">
                        <label>Email *</label>
                        <input type="email" name="email" className="form-control" required/>
                      </div>
                      <div className="form-group">
                        <label>Telefone ou Skype</label>
                        <input type="text" name="phone" className="form-control" required/>
                      </div>
                      <div className="form-group">
                        <label>Empresa</label>
                        <input type="text" name="company" className="form-control"/>
                      </div>
                      <div className="form-group">
                        <label>CPF ou CNPJ *</label>
                        <input type="text" name="registration" className="form-control" required/>
                      </div>
                      <div className="form-group">
                        <label>Voc j tem domnio? *</label><br />
                        <div className="custom-control custom-radio custom-control-inline">
                          <input className="custom-control-input" type="radio" name="hasDomain" id="domainYes" value="domainYes" onChange={this.handleChange} required/>
                          <label className="custom-control-label" htmlFor="domainYes">Sim</label>
                        </div>
                        <div className="custom-control custom-radio custom-control-inline">
                          <input className="custom-control-input" type="radio" name="hasDomain" id="domainNo" value="domainNo" onChange={this.handleChange} required/>
                          <label className="custom-control-label" htmlFor="domainNo">No</label>
                        </div>
                      </div>
                      {this.state.hasDomain !== null ? (
                        <div className="form-group">
                          <label>{this.state.hasDomain ? 'Informe o nome do domnio que voc j possui:' : 'Informe o nome de domnio que voc deseja:'}</label>
                          <input type="text" name="domain" className="form-control" placeholder="Exemplo: meudominio.com.br" required/>
                        </div>
                      ) : null}
                      <button type="submit" className="btn btn-primary" disabled={this.state.loading}>
                        Solicitar
                      </button>
                    </form>
                    <Link to="/"><FeatherIcon icon="arrow-left" size={16}/> Voltar</Link></div>
                  )}
                </div>
              </div>
            </div>
            <div className="row justify-content-end no-gutters position-md-absolute" style={{zIndex: 0, width: '100%', height: '100%', top: 0}}>
              <div className="col-12 col-md-5 col-lg-7 bg-secondary d-flex align-items-center pl-md-5" >
                <div>
                  <h2><strong>Como funciona</strong></h2>
                  <h4>1. Briefing</h4>
                  <p>blalbalbla</p>
                  <h4>2. Desenvolvimento</h4>
                  <p>blalbalbla</p>
                  <h4>3. Deploy</h4>
                  <p>blalbalbla</p>
                </div>
              </div>
            </div></section>
          </div>
        )
    }
  }

  export default Request



